import { Search } from 'lucide-react';

export default function ConversationList({ conversations, selectedConversation, onSelectConversation }) {
  return (
    <div className="w-1/4 border-r border-gray-200 bg-white overflow-y-auto">
      <div className="p-4 border-b border-gray-200">
        <h1 className="text-xl font-bold text-blue-600">Smart Inbox</h1>
        <div className="mt-4 relative">
          <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <input 
            type="text" 
            placeholder="Search conversations..." 
            className="w-full pl-10 pr-4 py-2 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>
      </div>
      
      <div className="p-2">
        {conversations.map(conversation => (
          <div 
            key={conversation.id}
            onClick={() => onSelectConversation(conversation)}
            className={`p-3 rounded-lg mb-2 cursor-pointer transition-colors ${
              selectedConversation?.id === conversation.id 
                ? 'bg-blue-50 border-l-4 border-blue-500' 
                : 'hover:bg-gray-100'
            } ${conversation.unread ? 'font-semibold' : ''}`}
          >
            <div className="flex justify-between items-start">
              <div className="flex-1">
                <div className="flex items-center">
                  <span className={`w-2 h-2 rounded-full mr-2 ${
                    conversation.priority === 'High' ? 'bg-red-500' : 
                    conversation.priority === 'Medium' ? 'bg-yellow-500' : 'bg-green-500'
                  }`}></span>
                  <h3 className="text-sm truncate">{conversation.customer.name}</h3>
                </div>
                <p className="text-xs text-gray-500 truncate">{conversation.customer.email}</p>
              </div>
              <span className="text-xs text-gray-400">{conversation.lastActivity}</span>
            </div>
            <p className="text-sm mt-1 font-medium truncate">{conversation.subject}</p>
            <p className="text-xs mt-1 text-gray-500 truncate">
              {conversation.messages[conversation.messages.length - 1].content.substring(0, 60)}...
            </p>
          </div>
        ))}
      </div>
    </div>
  );
} 